import React, { useState } from 'react';
import { Button, FloatingLabel, Form, ProgressBar, Spinner, Modal } from 'react-bootstrap';
import { FiInfo } from 'react-icons/fi';
import ProfileCompleteHeader from '../../../../components/molecules/Headers/ProfileCompletionHeader';
import colorsFinal from "../../../../../../theme/colors";

export default function BankDetails(props, route) {

    const [name, setName] = React.useState('');
    const [value, setValue] = React.useState('');
    const [loading, setloading] = React.useState(true);

    // dialog
    const [dshow, setdshow] = React.useState(false);
    const [ddata, setddata] = React.useState([]);

    const data = [
        { key: 0, gender: 'Savings' },
        { key: 1, gender: 'Current' }
    ]

    React.useEffect(() => {
        setloading(false)
    })

    if (loading) {
        return (
            <div className="container">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            <ProfileCompleteHeader
                header="Bank Details"
                navigation={props.history}
                percentage={50}
            />
            <Modal
                show={dshow}
                onHide={() => setdshow(!dshow)}
                backdrop="static"
                keyboard={false}
                size="sm"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <p className="textHead">{ddata[0]}</p>
                    <p className="textNormal">{ddata[1]}</p>
                </Modal.Body>
                <Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
                    Okay
                </Button>
            </Modal>
            <FiInfo
                style={{ position: 'absolute', top: "26px", left: "150px" }}
                size={15}
                className="primary"
                onClick={() => {
                    setddata(["", "Please use a bank account where you are either the sole holder or a primary/secondary holder in a joint account."]);
                    setdshow(true);
                }}
            />
            <div style={{ marginTop: 15 }}>
                <div className="floating-form">
                    <div className="floating-label">
                        <input className="floating-input" pattern="\d" type="number" maxLength={16} id="account" placeholder=" " />
                        <label className="floating-label-input" for="account">Account number</label>
                    </div>
                </div>
                <p className="textHeadauth" >Gender</p>
                {data.map((obj) => (
                    <div key={obj.key} style={{ display: 'flex', flexDirection: 'row' }}>

                        <div className="radio-item">
                            <input
                                type="radio"
                                id={obj.key}
                                name="ritem"
                                value={obj.key}
                                onChange={(val) => {
                                    setValue(val.target.value)
                                    console.log(val.target.value)
                                }}
                                className="textNormal"
                            />
                            <label className="textNormal radio-label" for={obj.key}>{obj.gender}</label>
                        </div>
                    </div>
                ))}
                <div className="floating-form">
                    <div className="floating-label">
                        <input className="floating-input" type="text" style={{ textTransform: 'uppercase' }} maxLength={11} id="ifsc" placeholder=" " />
                        <label className="floating-label-input" for="ifsc">IFSC</label>
                    </div>
                    <div className="dropdown">
                        <input className="dropdown-select" list="browsers" name="browser" placeholder="Bank name" />
                        <datalist id="browsers">
                            <option value="Internet Explorer" />
                            <option value="Firefox" />
                            <option value="Chrome" />
                            <option value="Opera" />
                            <option value="Safari" />
                        </datalist>
                        {/* {show ? (<FiChevronDown className="i" onClick={()=> setShow(!show)} />) : (<FiChevronUp className="i" onClick={()=> setShow(!show)} />)} */}
                    </div>
                </div>
            </div>
            <div className="footer">
                <Button variant="primary" className="btn-footer" onClick={() => { props.history.push('/complete_pro/doc') }} >
                    Next &#8594;
                </Button>
            </div>
        </div>
    }
}

