import React from 'react';
import { ProgressBar } from 'react-bootstrap';
import { FiArrowLeft, FiInfo } from "react-icons/fi";
import cssVar from '../../../styles';

function ProfileCompleteHeader({
   screenType,
   percentage,
   header,
   navigation,
   showInfo,
   goBack
}) {
   return (
      <div>
         <div style={{
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'space-between',
            padding: 0,
            marginTop: 15
         }}>

            {screenType === 'Home' ? null : (
               <FiArrowLeft style={{ marginRight: "10px", color: cssVar.getPropertyValue('--primary') }} size={20} onClick={() => navigation.goBack()} />
            )}
            <text style={{ flex: 1 }} className="textHead">{header}</text>

            {showInfo !== undefined ? (
               <FiInfo />
            ) : null}
            <text
               onClick={() => navigation.push('/dashboard')}
               className="textNormal" style={{ color: cssVar.getPropertyValue('--primary') }}>Exit</text>
         </div>
         <div className="Progress">
            <style type="text/css">
               {`
					.progress-bar {
						background-color: #00A6A4;
						color: white;
                  border-radius: 10px !important;
					}
				`}
            </style>
            <ProgressBar now={percentage} style={{ borderRadius: 10 }} className="progress-bar-custom" />
         </div>
      </div>
   );
};

export default ProfileCompleteHeader;
