import React from 'react';
import { Switch } from 'react-router-dom';
import { Route } from 'react-router-dom/cjs/react-router-dom.min';
import AuthProtectedRoutes from '../components/molecules/Routers/AuthProtectedRoutes';
import ProtectedRoutes from '../components/molecules/Routers/ProtectedRoutes';
import Home from '../screens/marketing/Home';
import EmailScreen from '../screens/mintboxpro/AuthScreen/EmailScreen';
import EnterPassScreen from '../screens/mintboxpro/AuthScreen/EnterPassScreen';
import NewPasswordScreen from '../screens/mintboxpro/AuthScreen/NewPasswordScreen';
import OTPScreen from '../screens/mintboxpro/AuthScreen/OTPScreen';
import ResetPasswordScreen from '../screens/mintboxpro/AuthScreen/ResetPasswordScreen';
import SetupPasswordScreen from '../screens/mintboxpro/AuthScreen/SetupPasswordScreen';
import SignupScreen from '../screens/mintboxpro/AuthScreen/SignupScreen';
import Address from '../screens/mintboxpro/CompleteProfile/Common_forms/Address';
import BankDetails from '../screens/mintboxpro/CompleteProfile/Common_forms/BankDetails';
import DocUpload from '../screens/mintboxpro/CompleteProfile/Common_forms/DocUpload';
import Fatca from '../screens/mintboxpro/CompleteProfile/Common_forms/Fatca';
import Nominee from '../screens/mintboxpro/CompleteProfile/Common_forms/Nominee';
import PersonalDetails from '../screens/mintboxpro/CompleteProfile/Common_forms/PersonalDetails';
import PanScreen from '../screens/mintboxpro/CompleteProfile/PanScreen';
import DashScreen from '../screens/mintboxpro/DashboardScreen/DashScreen';
import MenuScreen from '../screens/mintboxpro/DashboardScreen/MenuScreen';
import PortfolioScreen from '../screens/mintboxpro/PortfolioScreen';
import FundScreen from '../screens/mintboxpro/PortfolioScreen/FundScreen';
import GoalScreen from '../screens/mintboxpro/PortfolioScreen/GoalScreen';
import MintboxScreen from '../screens/mintboxpro/PortfolioScreen/MintboxScreen';



const HomeLayout = () => {
  return (
    <Switch>

      {/* -------- Route is for MarketingScreens -------- */}
      <Route exact path={`/`} component={Home} />

      {/* -------- AuthProtectedRoutes is for all AuthScreens -------- */}
      <AuthProtectedRoutes path={`/email`} component={EmailScreen} />
      <AuthProtectedRoutes path={`/login`} component={EnterPassScreen} />
      <AuthProtectedRoutes path={`/signup`} component={SignupScreen} />
      <AuthProtectedRoutes path={`/otp_screen`} component={OTPScreen} />
      <AuthProtectedRoutes path={`/setup_pass`} component={SetupPasswordScreen} />
      <AuthProtectedRoutes exact path={`/reset_pass`} component={ResetPasswordScreen} />
      <AuthProtectedRoutes path={`/reset_pass/:email/:token/:clientId`} component={NewPasswordScreen} />

      {/* -------- ProtectedRoutes is for all Mintbox Screens -------- */}
      <ProtectedRoutes exact path={`/menu`} component={MenuScreen} />
      <ProtectedRoutes exact path={`/dashboard`} component={DashScreen} />
      <ProtectedRoutes exact path={`/portfolio`} component={PortfolioScreen} />
      <ProtectedRoutes exact path={`/portfolio/fundScreen`} component={FundScreen} />
      <ProtectedRoutes exact path={`/portfolio/mintboxScreen`} component={MintboxScreen} />
      <ProtectedRoutes exact path={`/portfolio/goalScreen`} component={GoalScreen} />

      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/pan`} component={PanScreen} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/personal_details`} component={PersonalDetails} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/address`} component={Address} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/fatca`} component={Fatca} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/nominee`} component={Nominee} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/bank`} component={BankDetails} />
      <ProtectedRoutes bottom_tab={false} exact path={`/complete_pro/doc`} component={DocUpload} />
    </Switch>
  );
};

export default HomeLayout;
