import React, { useState } from 'react';
import { Spinner, Button } from 'react-bootstrap';
import { IoIosHelpCircleOutline } from 'react-icons/io';
import { HiDotsVertical } from 'react-icons/hi';
import { CurrencyFormat } from '../../../components/atoms/CurrencyFormat';
import TabBar from '../../../components/atoms/TabBar';
import PortfolioCard from '../../../components/molecules/Cards/PortfolioCard';
import MainHeader from '../../../components/molecules/Headers/MainHeader';
import cssVar from '../../../styles';
const FundScreen = (props) => {
  const [loading, setloading] = React.useState(true);
  const [data, setfundData] = useState({});
  const [isFabOpen, setIsFabOpen] = useState(false);

  React.useEffect(() => {
    console.log(JSON.parse(props.location.state.data))
    setfundData(JSON.parse(props.location.state.data));
    setloading(false);
  }, []);

  const onFabPressed = () => {
    setIsFabOpen((o) => !o);
  };

  const FundTabs = () => {
    const TransactionsWrapper = () => <div>Transaction List</div>;
    const MonthlySip = () => <div>MonthlySip List</div>;
    const OtherDetails = () => <div>OtherDetails List</div>;
    const STPs = () => <div>STPs List</div>;
    const SWPs = () => <div>SWPs List</div>;

    const keyTitles = [
      { key: 'MonthlySip', title: 'SIPs' },
      { key: 'STPs', title: 'STPs' },
      { key: 'SWPs', title: 'SWPs' },
      { key: 'TransactionsWrapper', title: 'Transactions' },
      { key: 'OtherDetails', title: 'Additional' }
    ];

    const keyComponents = { MonthlySip, TransactionsWrapper, OtherDetails, STPs, SWPs };

    return (
      <TabBar keyTitles={keyTitles} keyComponents={keyComponents} />
    );
  };

  if (loading) {
    return (
      <div className="container">
        <Spinner
          as="span"
          animation="grow"
          size="sm"
          role="status"
          aria-hidden="true"
        />
      </div>
    );
  } else {

    return (
      <div style={{ flex: 1 }}>
        <MainHeader
          navigation={props.history}
          screenType="FundDetails"
          header={{
            name: data.SchemeName,
            type: `${data.AssetType} | NAV ${CurrencyFormat({ type: '2', num: data.PurchaseNAV })}`
          }}
          right="upload" />
        {isFabOpen ?
          <span style={{ backgroundColor: '#00000077', position: 'absolute', height: '100%', width:"100%", bottom:120, zIndex:1}}>
          <div style={{ position: 'fixed', width: '50%', bottom: 120, right: 20 }}>
            <Button className="btn-normalBorder" style={{ marginBottom: "10px" }}>
              Reedme
            </Button>
            <Button className="btn-normalBorder" style={{ marginBottom: "10px" }}
            >
              Switch
            </Button>
            <Button className="btn-normalBorder" style={{ marginBottom: "10px" }}
            >
              Start SIP
            </Button>
            <Button className="btn-normalBorder" style={{ marginBottom: "10px" }}
            >
              Start STP
            </Button>
            <Button className="btn-normalBorder" style={{ marginBottom: "10px" }}
            >
              Start SWP
            </Button>
          </div>
        </span> : null}

        <div className="copycontainer_top">
          {/* <KeyboardAvoidingView style={{ flex: 10 }} behavior="height">
          <ScrollView
            keyboardShouldPersistTaps="handled"
            style={s.copycontainer}
            onScroll={Animated.event(
              [{ nativeEvent: { contentOffset: { y: offset } } }],
              { useNativeDriver: false }
            )}
            showsVerticalScrollIndicator={false}> */}
          <PortfolioCard
            type="fund"
            header={false}
            navigation={props.history}
            data={[
              data.CurrentBalance,
              data.DailyChangeAmnt,
              `${data.Units}`,
              `${data.DailyChangePerc}`,
              data.CurrentNAV,
              `${data.FundXIRR}`
            ]} />
          <FundTabs />
          {/* </ScrollView> */}
          <div
            className="footer"
            style={{
              marginBottom: "40px",
              width: '100%',
              display: "flex",
              paddingRight: "15px",
              paddingLeft: "15px"
            }}>
            <Button className="btn-normal"
            >
              Invest more
            </Button>
            <span
              onClick={onFabPressed}
              style={{
                marginLeft: 20,
                padding: 10,
                borderWidth: 1,
                border:"1px solid",
                borderColor: cssVar.getPropertyValue("--divider"),
                borderRadius: 500,
              }}>
              {isFabOpen ? <i style={{ fontSize: "18px", paddingLeft:5,paddingRight: 5, paddingTop: 3 }} class="fa">&#x2715;</i> : <HiDotsVertical className="icon-def" size={24} />}
            </span>
          </div>
          {/* </KeyboardAvoidingView> */}
        </div>
      </div> 
    );
  }
};

export default FundScreen;
