import React from 'react';
import { Tab, Tabs } from "react-bootstrap";

export default function TabBar(props) {
    const { style, keyTitles = [], keyComponents = {}, right = false, left = false } = props;
    const [index, setIndex] = React.useState(keyTitles[0].key);
    const [routes] = React.useState(keyTitles);
    // const renderScene = SceneMap(keyComponents);
console.log(keyComponents["user"])
    const CustTabBar = () => (
        keyTitles.map((data, index) => {
            return <Tab eventKey={data.key} title={data.title}>
                <div>{
                    keyComponents[data.key]()
                }</div>
            </Tab>
        })
    );

    return (
        <Tabs defaultActiveKey={index} className="mb-3">
                {CustTabBar()}    
        </Tabs >
    );
}
