import React, { useState } from 'react'
import './toggle.css'

export default function ToggleButton({ checked=false, onClick=() => {} }) {
  const [isChecked, setIsChecked] = useState(checked);
  
  const handleChange = (event) => {
    event.preventDefault();

    setIsChecked(s => !s)
    onClick();

    // event.stopPropagation();

  }

  return (
    <div className="toggle-container">
      <label>
        <input className="toggle" checked={isChecked} onChange={handleChange} 
          type="checkbox" />
        <div>
          <div></div>
        </div>
      </label>
    </div>
  )
}

